import React, { useState } from "react";
import EchoPixelPopup from "../components/echopixelpopup";
import echopixelImage from "./../images/echopixelImage.jpg";
import blibliImage from "./../images/blibliImage.jpg";
import yanaImage from "./../images/YanaImage.png";
import technosoftImage from "./../images/technosoftImage.png";
import "../styling/experience.css";
import BlibliPopup from "../components/bliblipopup";
import YanaPopup from "../components/yanapopup";
import TechnosoftPopup from "../components/technosoftpopup";

function Experience() {
  const [showEchoPixelPopup, setShowEchoPixelPopup] = useState(false);
  const toggleEchoPixelPopup = () => {
    setShowEchoPixelPopup(!showEchoPixelPopup);
  };
  const [showBlibliPopup, setShowBlibliPopup] = useState(false);
  const toggleBlibliPopup = () => {
    setShowBlibliPopup(!showBlibliPopup);
  };
  const [showYanaPopup, setShowYanaPopup] = useState(false);
  const toggleYanaPopup = () => {
    setShowYanaPopup(!showYanaPopup);
  };
  const [showTechnosoftPopup, setShowTechnosoftPopup] = useState(false);
  const toggleTechnosoftPopup = () => {
    setShowTechnosoftPopup(!showTechnosoftPopup);
  };
  return (
    <div className="container-content">
      <div className="wrapper">
        <div className="title-education">
          <h2>Professional Experience</h2>
        </div>
      </div>
      <div className="card-view">
        <div className="card">
          <button className="ExperiencePopUp" onClick={toggleEchoPixelPopup}>
            <div className="image-crop">
              <img
                className="button-image"
                src={echopixelImage}
                alt="Description"
              />
            </div>

            <div className="card-text">
              <div className="card-text-entry">
                <h3>Echopixel, Inc. (www.echopixeltech.com)</h3>
              </div>
              <div className="card-text-entry">
                <h3>May 2023 - July 2023</h3>
              </div>
            </div>
          </button>
          <EchoPixelPopup
            show={showEchoPixelPopup}
            onClose={() => setShowEchoPixelPopup(false)}
          />
        </div>
        <div className="card">
          <button className="ExperiencePopUp" onClick={toggleBlibliPopup}>
            <div className="image-crop">
              <img
                className="button-image"
                src={blibliImage}
                alt="Description"
              />
            </div>
            <div className="card-text">
              <div className="card-text-entry">
                <h3>Blibli PT Global Digital Niaga (www.blibli.com)</h3>
              </div>
              <div className="card-text-entry">
                <h3>July 2022 – August 2022</h3>
              </div>
            </div>
          </button>
          <BlibliPopup
            show={showBlibliPopup}
            onClose={() => setShowBlibliPopup(false)}
          />
        </div>
      </div>
      <div className="card-view">
        <div className="card">
          <button className="ExperiencePopUp" onClick={toggleYanaPopup}>
            <div className="image-crop">
              <img className="button-image" src={yanaImage} alt="Description" />
            </div>

            <div className="card-text">
              <div className="card-text-entry">
                <h3>Technosoft Automotive (www.technosoftautomotive.com)</h3>
              </div>
              <div className="card-text-entry">
                <h3>May 2022 – June 2022</h3>
              </div>
            </div>
          </button>
          <YanaPopup
            show={showYanaPopup}
            onClose={() => setShowYanaPopup(false)}
          />
        </div>
        <div className="card">
          <button className="ExperiencePopUp" onClick={toggleTechnosoftPopup}>
            <div className="image-crop">
              <img
                className="button-image"
                src={technosoftImage}
                alt="Description"
              />
            </div>
            <div className="card-text">
              <div className="card-text-entry">
                <h3>Technosoft Automotive (www.technosoftautomotive.com)</h3>
              </div>
              <div className="card-text-entry">
                <h3>June 2021 – August 2021</h3>
              </div>
            </div>
          </button>
          <TechnosoftPopup
            show={showTechnosoftPopup}
            onClose={() => setShowTechnosoftPopup(false)}
          />
        </div>
      </div>
    </div>
  );
}

export default Experience;
