import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { SuperSEO } from "react-super-seo";
import "../styling/about.css";

function About() {
  return (
    <div className="container-about">
      <div className="about">
        <div className="purpose">
          <p>
            Hello! I am a Computer Science and Mathematics major at the
            University of Illinois, Urbana-Champaign. I have strong proficiency
            in full stack development and heavy interest in Machine learning and
            Computer Vision, more specifically Object Detection. Through my
            coursework at UIUC, I have gained a strong foundation for
            theoretical and technical computer science, with the addition of
            pure mathematics courses which contributed to my problem solving and
            logical thinking skills. I have applied and further solidified my
            knowledge through multiple internship experiences, learning various
            lagnuages and frameworks in the process.
          </p>
        </div>
      </div>
    </div>
  );
}

export default About;
